 the success you deserve is you, yourself.\" I strongly believe that one can excel to their maximum potential merely by embracing every opportunity that knocks on their door. Rotaract is one such goblet of opportunities! The movement enables one to explore and learn what they love doing the most and gives innumerable chances to prove their mettle and outshine themselves. As a person, I always believe that stepping out of one's comfort zone helps identify oneself and leads towards success. With exploration, what comes handy is consistency - never shying away from challenging oneself, is a quality, I feel is of utmost importance for every Rotaractor to possess. Working with people from various walks of life, it is essential for one to be compassionate towards their peers and try to place themselves in the other's shoe before acting upon a particular situation. The key to earning people's respect is to avoid disputes and always be accommodating of the other person, making them feel respected. It is not important for people to remember us by our names, but it is imperative to make a mark with our deeds, and that is something I feel we all should be working towards, everyday. Let's together make this world a better place to live in - for ourselves and for others. Vamos! ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"body_wrapper"},[_c('div',{staticClass:"body_inner_left_holder"},[_c('article',{staticClass:"material-card Red"},[_c('h2',[_c('span',[_vm._v("Rtn. Rtr. Rahesh Donny")]),_c('strong',{staticStyle:{"font-size":"15px"}},[_vm._v(" Immediate Past District Rotaract Representative ")])]),_c('div',{staticClass:"mc-content"},[_c('div',{staticClass:"img-container"},[_c('img',{staticClass:"img-responsive",attrs:{"src":require("../../assets/ct/Rahesh.jpg")}})])])])]),_c('div',{staticClass:"body_inner_right",staticStyle:{"display":"block"}},[_c('i',[_vm._v("“The measure of intelligence is the ability to ch